<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process83"></a>

<p><br></p>
<p><span class="rvts17">空白技能书（</span><span class="rvts50">Blank Skill Books</span><span class="rvts17">）：</span>
<br><span class="rvts21">原罪</span><span class="rvts20">2</span><span class="rvts21">进入正式版本后，空白技能书只能在商人处买到（无法合成）。空白技能书对应的有自己的派系，因此只能与其对应派系的卷轴合成相应的技能书。</span>
<br><span class="rvts21">另外，空白技能书也有等级之说，分为一级二级三级空白技能书，并可以与其对应的相同等级的魔法卷轴合成为新的魔法技能书。</span>
<br><span class="rvts18"><br></span></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



